/** @format */

section.Twitter {
  width: 100%;
  height: 100vh;
  padding-top: 20px;
  font-family: Roboto, "sans-serif";
}

.link_social {
  margin: 0px 0px 10px 10%;
  opacity: 0.7;
  font-size: 15px;
  color: #2e3239;
}

.Icons {
  width: 100%;
  display: grid;
  grid-template-columns: repeat(4, 1fr);
}

.Link_Progress {
  position: relative;
  grid-template-columns: 1fr 1fr 1fr;
  padding-left: 10%;
}

.Link_Progress .Line {
  position: absolute;
  top: 50%;
  left: 0;
  width: 45%;
  height: 3px;
  background: #12b0ee;
}

.Skip_Button {
  align-self: center;
}

.Skip_Button a {
  text-decoration: underline;
  opacity: 0.7;
  font-family: Roboto;
  font-size: 13px;
  color: #24272c;
}

.Icon_Grid .Icon {
  width: 40px;
  border-radius: 3px;
  z-index: 1;
}

.Link_Progress .Icon {
  mix-blend-mode: luminosity;
  opacity: 0.5;
}

.Icon_Grid {
  display: grid;
  grid-template-columns: 70% 30%;
}

.Icon_Grid .Icon:nth-child(1),
.Icon_Grid .Icon:nth-child(2),
.Icon_Grid .Icon:nth-child(3) {
  mix-blend-mode: unset !important;
  opacity: 1 !important;
}

.Icon_Grid .Icon:nth-child(3) {
  border: 4px solid #12b0ee;
  border-radius: 10px;
  padding: 1px;
  box-shadow: 0 -1px 5px 0 rgba(0, 0, 0, 0.15);
}

.Form_Header {
  padding-top: 42px;
  display: grid;
  grid-template-columns: 15% 80%;
}

.Form_Header .Back_Btn {
  margin-top: 5px;
  align-self: baseline;
  text-align: center;
}

.Header_Text h1 {
  text-align: start;
  font-size: 24px;
  font-weight: 500;
  line-height: 1.17;
  letter-spacing: normal;
  color: #3b414a;
}

.Header_Text p:nth-child(2) {
  margin-top: 10px;
  opacity: 0.7;
  font-family: Roboto;
  font-size: 14px;
  line-height: 1.43;
  color: #24272c;
}

.Header_Text p:nth-child(3) {
  font-size: 14px;
  font-weight: 500;
  color: #656f7a;
  margin-top: 8px;
  text-decoration: underline;
}

.Main {
  margin: 10px 0 0 15%;
}

.Twitter_Btn {
  display: flex;
  justify-content: space-around;
  width: 50%;
  border-radius: 4px;
  background-color: #50abf1;
  align-items: center;
  padding: 10px;
  margin: 20px 0px 0px 15%;
  font-size: 15px;
}

.Twitter_Btn img {
  width: 30px;
  margin-right: 10px;
}
